"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Switch } from "@/components/ui/switch";
import { Label } from "@/components/ui/label";

export default function UpgradePlanPage() {
  const [isAnnual, setIsAnnual] = useState(false);

  const plans = [
    {
      name: "Free Trial",
      price: {
        monthly: 0,
        annual: 0,
      },
      description: "Perfect for trying out our services",
      features: [
        "7 days access",
        "Basic content generation",
        "5 AI images per day",
        "3 scripts per day",
        "Email support",
      ],
      limit: "7-day trial only",
      buttonText: "Current Plan",
      disabled: true,
    },
    {
      name: "Professional",
      price: {
        monthly: 49,
        annual: 39,
      },
      description: "For individual marketers and small teams",
      features: [
        "Unlimited access",
        "Advanced content generation",
        "50 AI images per day",
        "Unlimited scripts",
        "Priority email support",
        "API access",
      ],
      limit: "For up to 2 team members",
      buttonText: "Upgrade Now",
      disabled: false,
      popular: true,
    },
    {
      name: "Enterprise",
      price: {
        monthly: 99,
        annual: 79,
      },
      description: "For marketing teams and agencies",
      features: [
        "Everything in Professional",
        "Unlimited AI images",
        "Custom templates",
        "Team collaboration",
        "24/7 priority support",
        "Custom API limits",
      ],
      limit: "Unlimited team members",
      buttonText: "Contact Sales",
      disabled: false,
    },
  ];

  const handleUpgrade = async (planName: string) => {
    // Integrate with SSLCOMMERZ payment gateway
    try {
      const response = await fetch("/api/create-payment", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          plan: planName,
          billingCycle: isAnnual ? "annual" : "monthly",
        }),
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.message || "Failed to initiate payment");
      }

      // Redirect to SSLCOMMERZ payment page
      window.location.href = data.paymentUrl;
    } catch (error) {
      console.error("Error initiating payment:", error);
    }
  };

  return (
    <div className="max-w-6xl mx-auto space-y-8 p-6">
      <div className="text-center space-y-4">
        <h1 className="text-4xl font-bold">Upgrade Your Plan</h1>
        <p className="text-xl text-muted-foreground max-w-2xl mx-auto">
          Choose the perfect plan for your marketing needs
        </p>
      </div>

      {/* Billing Toggle */}
      <div className="flex items-center justify-center space-x-4">
        <Label htmlFor="billing-toggle" className="text-muted-foreground">
          Monthly
        </Label>
        <Switch
          id="billing-toggle"
          checked={isAnnual}
          onCheckedChange={setIsAnnual}
        />
        <div className="flex items-center space-x-2">
          <Label htmlFor="billing-toggle" className="text-muted-foreground">
            Annual
          </Label>
          <span className="bg-primary/10 text-primary text-sm px-2 py-1 rounded-full">
            Save 20%
          </span>
        </div>
      </div>

      {/* Pricing Cards */}
      <div className="grid md:grid-cols-3 gap-6 mt-8">
        {plans.map((plan) => (
          <Card
            key={plan.name}
            className={`relative ${
              plan.popular
                ? "border-primary shadow-lg scale-105"
                : "border-border"
            }`}
          >
            {plan.popular && (
              <div className="absolute -top-4 left-1/2 -translate-x-1/2">
                <span className="bg-primary text-primary-foreground text-sm px-3 py-1 rounded-full">
                  Most Popular
                </span>
              </div>
            )}
            <CardHeader>
              <CardTitle className="text-2xl">{plan.name}</CardTitle>
              <div className="mt-4">
                <span className="text-4xl font-bold">
                  ${isAnnual ? plan.price.annual : plan.price.monthly}
                </span>
                <span className="text-muted-foreground">/month</span>
                {isAnnual && (
                  <p className="text-sm text-muted-foreground mt-1">
                    Billed annually
                  </p>
                )}
              </div>
            </CardHeader>
            <CardContent className="space-y-6">
              <p className="text-muted-foreground">{plan.description}</p>
              <ul className="space-y-3">
                {plan.features.map((feature, index) => (
                  <li key={index} className="flex items-center">
                    <svg
                      className="w-4 h-4 mr-2 text-primary"
                      fill="none"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth="2"
                      viewBox="0 0 24 24"
                      stroke="currentColor"
                    >
                      <path d="M5 13l4 4L19 7" />
                    </svg>
                    {feature}
                  </li>
                ))}
              </ul>
              <p className="text-sm text-muted-foreground">{plan.limit}</p>
              <Button
                className="w-full"
                variant={plan.popular ? "default" : "outline"}
                disabled={plan.disabled}
                onClick={() => handleUpgrade(plan.name)}
              >
                {plan.buttonText}
              </Button>
            </CardContent>
          </Card>
        ))}
      </div>

      {/* FAQ Section */}
      <div className="mt-16 space-y-4">
        <h2 className="text-2xl font-bold text-center">
          Frequently Asked Questions
        </h2>
        <div className="grid md:grid-cols-2 gap-6 mt-8">
          <Card>
            <CardContent className="p-6">
              <h3 className="font-semibold mb-2">
                Can I change plans at any time?
              </h3>
              <p className="text-muted-foreground">
                Yes, you can upgrade, downgrade, or cancel your plan at any time.
                Changes take effect at the start of the next billing cycle.
              </p>
            </CardContent>
          </Card>
          <Card>
            <CardContent className="p-6">
              <h3 className="font-semibold mb-2">
                What payment methods do you accept?
              </h3>
              <p className="text-muted-foreground">
                We accept all major credit cards, debit cards, and mobile banking
                through SSLCOMMERZ payment gateway.
              </p>
            </CardContent>
          </Card>
          <Card>
            <CardContent className="p-6">
              <h3 className="font-semibold mb-2">Is there a refund policy?</h3>
              <p className="text-muted-foreground">
                Yes, we offer a 30-day money-back guarantee if you're not satisfied
                with our service.
              </p>
            </CardContent>
          </Card>
          <Card>
            <CardContent className="p-6">
              <h3 className="font-semibold mb-2">
                Do you offer custom enterprise plans?
              </h3>
              <p className="text-muted-foreground">
                Yes, contact our sales team for custom plans tailored to your
                organization's specific needs.
              </p>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}
